.my{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    float: right;
    align-items: flex-end;
    margin-right: 2rem;
}

.message{
    height: 45rem;
    overflow-y: auto;
}

.text{
    border-radius: 2rem;
    padding: 1rem;
    padding-top: .2rem;
    padding-bottom: .2rem;
}

.text p{
    font-weight: 100;
}

.other{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 2rem;
}
